html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-size: contain;
  backdrop-filter: blur(10px);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.webp body {
  background-image: url(/build/img/bg.webp);
}

.no-webp body {
  background-image: url(/build/img/bg.pbg);
}

p {
  color: #000000;
  font-size: 2rem;
}

.navegacion {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 12rem;
}
.navegacion a {
  margin-right: 5rem;
  font-weight: 700;
  color: #FFFFFF;
}

.contenedor {
  width: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 3rem 0 3rem 0;
}

.contenido {
  background-color: rgba(255, 255, 255, 0.5);
  width: 100%;
  height: 80%;
  min-height: 700px;
  border-radius: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  padding: 4rem;
}

.btn {
  background-color: #770BBF;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  cursor: pointer;
  border-radius: 0.5rem;
}
.btn:hover {
  background-color: #680aa7;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3, h4 {
  margin: 0;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
  margin: 0 0 7rem 0;
}

h3 {
  font-size: 3rem;
  margin: 2rem 0;
}

h4 {
  font-size: 2.6rem;
}

.main {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.titulo-principal {
  height: 100vh;
  display: flex;
  flex-grow: 1;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}
.titulo-principal h1 {
  text-align: center;
  font-family: "Mr Dafoe", cursive;
  font-size: 20rem;
  font-weight: 400;
  color: #e1e1e1;
  line-height: 9rem;
}
.titulo-principal span {
  display: block;
  text-align: right;
  font-family: "Lato", sans-serif;
  font-size: 4rem;
  font-weight: 400;
  color: #000000;
}

#sobre-mi p {
  margin-bottom: 12rem;
}

.habilidades-grid {
  width: 100%;
  display: grid;
  gap: 2.5rem;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 4rem;
}

.habilidad {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.habilidad .icon {
  width: 5rem;
}
.habilidad .icon i {
  font-size: 5rem;
}
.habilidad .icon img {
  width: 5rem;
}

.proyectos {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.card {
  background-color: #e1e1e1;
  overflow: hidden;
  border-radius: 2rem;
  width: 35rem;
  height: 25rem;
  cursor: pointer;
  transition: 0.4s;
  margin: 0 4rem;
}
.card .descripcion-proyecto {
  visibility: hidden;
  opacity: 0;
  transition: 0.2s;
}
.card:hover {
  height: 60rem;
}
.card:hover .descripcion-proyecto {
  visibility: visible;
  opacity: 1;
}
.card .btn {
  width: 50%;
  margin: 2rem;
}

.formulario {
  position: relative;
  background-color: #e1e1e1;
  border-radius: 2rem;
  padding: 3rem 3rem 3rem 20rem;
  display: flex;
  height: 40rem;
  align-items: center;
}
.formulario .inputs {
  display: flex;
  flex-direction: column;
  width: 50rem;
}
.formulario input, .formulario textarea {
  display: block;
  margin-bottom: 2rem;
  border: none;
  border-radius: 0.5rem;
  padding: 0.5rem;
}
.formulario input:last-of-type {
  margin-bottom: 0;
}
.formulario .nombre-apellido {
  display: flex;
  justify-content: space-between;
}
.formulario .nombre-apellido input {
  display: inline-flex;
}
.formulario .nombre-apellido input:last-of-type {
  margin-bottom: 2rem;
}
.formulario textarea {
  color: #8d8d8d;
}

.info-contacto {
  position: absolute;
  width: 30rem;
  left: -15rem;
  background-color: #079DD9;
  border-radius: 2rem;
  padding: 2rem;
}
.info-contacto p {
  font-size: 1.5rem;
}
.info-contacto a {
  color: #000000;
  font-size: 3rem;
}
.info-contacto .enlaces-contacto {
  display: flex;
  justify-content: space-around;
}