html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: $fuente_principal;
    font-size: 1.6rem;
    line-height: 1.8;
    background-size: contain;
    backdrop-filter: blur(10px);
    display: flex;
    flex-direction: column;
    align-items: center;
}

.webp body {
    background-image: url(/build/img/bg.webp);
}

.no-webp body {
    background-image: url(/build/img/bg.pbg);
}
p {
    color: $negro;
    font-size: 2rem;
}

.navegacion {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 12rem;

    a {
        margin-right: 5rem;
        font-weight: $bold;
        color: $blanco;
    }
}

.contenedor {
    // height: auto;
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 3rem 0 3rem 0;
}

.contenido {
    background-color: rgba($color: $blanco, $alpha: 0.5);
    width: 100%;
    height: 80%;
    min-height: 700px;
    border-radius: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    padding: 4rem;
}

.btn {
    @include boton($morado, block);
}

a {
    text-decoration: none;
}

img,
picture {
    width: 100%;
    display: block;
}

h1, h2, h3, h4 {
    margin: 0;
}

h1 {
    font-size: 3.8rem;
}
h2 {
    font-size: 3.4rem;
    margin: 0 0 7rem 0;
}
h3 {
    font-size: 3rem;
    margin: 2rem 0;
}
h4 {
    font-size: 2.6rem;
}